<template>
<div>
  
    <h2>illy咖啡品类销售top40</h2>
  <el-table
    :data="tableData3"
    border
    style="width: 100%">
    <el-table-column
      prop="url"
      label="商品链接"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="商品名称"
      width="180">
    </el-table-column>
    <el-table-column
      prop="category"
      label="商品分类">
    </el-table-column>
    <el-table-column
      prop="current_price"
      sortable
      label="商品现价">
    </el-table-column>
    <el-table-column
      prop="original_price"
      sortable
      label="商品原价">
    </el-table-column>
    <el-table-column
      prop="month_sales_count"
      sortable
      label="月销量">
    </el-table-column>
    <el-table-column
      prop="month_amount"
      sortable
      label="月交易额">
    </el-table-column>
    
    <el-table-column
      prop="totalSoldQuantity"
      sortable
      label="总销量">
    </el-table-column>
    <el-table-column
      prop="all_amount"
      sortable
      label="总交易额">
    </el-table-column>
     <el-table-column
      prop="stores_count"
      sortable
      label="收藏数">
    </el-table-column>
    <el-table-column
      prop="score"
      sortable
      label="评分">
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData3: []
    };
  },
  methods: {
    requestdata() {
      axios.get("../static/illytop.json").then(res => {
        this.tableData3 = res.data;
        console.log(res.data);
      });
    }
  },
  mounted() {
    this.requestdata();
  }
};
</script>


